@import'~node_modules/modern-normalize/modern-normalize.css';

.five-days-section {
  border-radius: 34px;
  background: rgba(16, 33, 54, .8);
  box-shadow: 0px 5px 33px 0px rgba(16, 33, 54, .6);
  max-width: 280px;
  margin: 0 auto;
  margin-top: 32px
}

.five-days-section__title {
  margin: 0;
  color: #fff;
  text-align: center;
  font-family: "Lato";
  font-size: 18px;
  font-weight: 700;
  padding-top: 32px
}

.cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  margin-left: 7px
}

.days-cards {
  display: flex;
  overflow: hidden;
  gap: 17px;
  align-self: baseline;
  padding-left: 21px;
  transition: all 1s ease-in-out
}

.card {
  width: 65px;
  display: flex;
  flex-direction: column;
  align-items: center
}

.card__name {
  color: #fff;
  text-align: center;
  font-family: "Lato";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: .48px;
  margin: 0;
  padding-top: 20px
}

.card__date {
  color: #fff;
  text-align: center;
  font-family: "Lato";
  font-size: 16px;
  font-weight: 400;
  letter-spacing: .64px;
  margin: 0;
  padding-top: 8px;
  padding-bottom: 4px
}

.card__button {
  color: #fff;
  text-align: center;
  font-family: "Lato";
  font-size: 12px;
  font-weight: 700;
  text-decoration-line: underline;
  background: none;
  border: none;
  opacity: .3000000119
}

.card__button:hover {
  color: #ff6b09;
  opacity: 1;
  cursor: pointer
}

.temperatures {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 4px;
  gap: 4px
}

.temperatures__item:last-child {
  border-left: 1px solid rgba(255, 255, 255, .2);
  padding-left: 4px
}

.temperatures__label {
  color: #767d85;
  text-align: center;
  font-family: "Lato";
  font-size: 12px;
  font-weight: 400
}

.temperatures__item {
  color: #ff6b09;
  text-align: center;
  font-family: "Lato";
  font-size: 14px;
  font-weight: 400;
  border-left: 1px #27384a
}

.more-info-container {
  display: flex;
  flex-direction: row;
  padding-top: 20px;
  gap: 25px;
  padding-bottom: 20px;
  align-self: baseline;
  transition: all 1s ease-in-out
}

.more-info {
  width: 120px;
  height: 217px;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, .2);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px
}

.more-info:hover {
  background: rgba(255, 255, 255, .1);
  cursor: pointer
}

.more-info__hour {
  margin: 0;
  color: #fff;
  text-align: center;
  font-family: "Lato";
  font-size: 14px;
  font-weight: 400;
  opacity: .5400000215;
  padding-top: 20px
}

.more-info__temperature {
  margin: 0;
  color: #ff6b09;
  text-align: center;
  font-family: "Lato";
  font-size: 18px;
  font-weight: 700
}

.details-list {
  margin: 0;
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 4px
}

.details-list__item {
  display: flex;
  gap: 10px;
  color: #fff;
  font-family: "Lato";
  font-size: 14px;
  font-weight: 400;
  opacity: .5400000215;
  align-items: center
}

.details-list__icon {
  fill: #fff
}

.buttons-container {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  gap: 50px
}

.arrow {
  fill: #fff
}

.arrow:hover {
  opacity: .5;
  cursor: pointer
}

@media screen and (min-width: 768px) {
  .days-cards {
    padding-left: 40px
  }

  .five-days-section {
    max-width: 704px;
    border-radius: none;
    background: none;
    box-shadow: none
  }

  .five-days-section__title {
    padding-top: 0;
    font-size: 28px;
    letter-spacing: 1.12px;
    padding-bottom: 24px
  }

  .cards {
    border-radius: 34px;
    background: rgba(16, 33, 54, .8);
    box-shadow: 0px 5px 33px 0px rgba(16, 33, 54, .6)
  }

  .card {
    width: 111px
  }

  .card__name {
    padding-top: 40px;
    font-size: 14px;
    letter-spacing: .56px
  }

  .card__date {
    font-size: 18px;
    letter-spacing: .72px
  }

  .card__button {
    font-size: 14px;
    padding-bottom: 40px
  }

  .temperatures {
    padding-top: 8px;
    padding-bottom: 8px;
    gap: 12px
  }

  .temperatures__item:last-child {
    padding-left: 12px
  }

  .temperatures__label {
    font-size: 14px
  }

  .temperatures__item {
    font-size: 18px;
    display: flex;
    flex-direction: column;
    gap: 4px
  }

  .more-info-container {
    gap: 16px;
    padding-bottom: 20px;
    padding-top: 0
  }

  .more-info__temperature {
    font-size: 18px
  }

  .buttons-container {
    display: none
  }
}

@media screen and (min-width: 1280px) {
  .five-days-section {
    max-width: 1248px;
    height: 307px
  }

  .five-days-section__title {
    padding-bottom: 32px
  }

  .cards {
    gap: 32px
  }

  .days-cards {
    padding-left: 120px
  }

  .card {
    width: 181px
  }

  .card__name {
    font-size: 18px;
    letter-spacing: .72px;
    padding-bottom: 8px
  }

  .card__date {
    font-size: 25px;
    letter-spacing: 1px;
    padding-bottom: 20px
  }

  .card__button {
    font-size: 14px;
    padding-bottom: 40px
  }

  .temperatures {
    padding-top: 32px;
    padding-bottom: 8px;
    gap: 30px
  }

  .temperatures__item:last-child {
    padding-left: 30px
  }

  .temperatures__label {
    font-size: 18px
  }

  .temperatures__item {
    font-size: 25px
  }

  .more-info-container {
    gap: 16px;
    padding-bottom: 20px;
    margin-left: 20px
  }

  .more-info {
    width: 152px;
    height: 217px
  }

  .more-info__temperature {
    font-size: 25px
  }
}

.hidden {
  display: none
}

.weather-container {
  display: flex;
  justify-content: center;
  margin: 40px 0px 32px 0px
}

.weather-container .weather-info {
  display: flex;
