}

.quote-container .quote .quote__container .quote__text {
  color: #fff;
  font-size: 14px;
  margin: 0;
  direction: rtl
}

.quote-container .quote .quote__container .quote__author {
  font-size: 18px;
  color: #fff;
  font-weight: 400;
  margin: 0
}

