.current-date-container {
  display: flex;
  justify-content: center;
  border-radius: 34px;
  margin-top: 32px
}

.current-date-container .date-info {
  width: 276px;
  height: 184px;
  display: flex;
  gap: 16px;
  flex-direction: column;
  background-color: #102136;
  opacity: 60%;
  justify-content: center;
  align-items: center;
  border-radius: 34px;
  padding-left: 0px;
  box-shadow: 0px 8px 42px 0px rgba(0, 0, 0, .75)
}

.current-date-container .date-info__date h3 {
  color: #fff;
  font-size: 45px;
  font-weight: 300
}

.current-date-container .date-info__details {
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: center
}

.current-date-container .date-info__details .time {
  display: flex;
  flex-direction: row;
  gap: 10px
}

.current-date-container .date-info__details .time p {
  font-size: 14px;
  color: #fff;
  font-weight: 400
}

.current-date-container .date-info__details .time .line {
  border: 1px solid #fff
}

.current-date-container .date-info__details .sun-details {
  display: flex;
  flex-direction: row;
  gap: 10px
}

.current-date-container .date-info__details .sun-details p {
  font-size: 14px;
  color: #fff;
  font-weight: 400
}

.current-date-container .date-info__details .sun-details .line {
  border: 1px solid #fff
}

.buttons {
